import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Todo from '@/components/Todo'
import Start from '@/components/Start'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/testView/',
      name: 'Main',
      component: Main
    },
    {
      path: '/testView/todo',
      name: 'Todo',
      component: Todo
    },
    {
      path: '/testView/start',
      name: 'Start',
      component: Start
    },
  ]
})



