<template>
  <div id="app">
    <h1>TODO</h1>
    <router-link to="/">Top</router-link>
    <ul class="todo">
      <li v-for='(defaultItem, index) in defaultItems' :key='index'>
        <label v-bind:class="{ done: defaultItem.isChecked }">
          <input type="checkbox" v-model="defaultItem.isChecked"> {{ defaultItem.title }}

        </label>
      </li>
    </ul>
    <ul>
      <li v-for='(item, index) in items' :key='index'>
        <label v-bind:class="{ done: item.isChecked }">
          <input type="checkbox" v-model="item.isChecked"> {{ item.title }}
        </label>
      </li>
    </ul>
    <p>
      <input type="text"
        placeholder="TODOを入力しましょう！"
        v-model="newItemTitle"
        v-on:keyup.enter="addTodo">
    </p>
    <button v-on:click="saveTodo()">保存</button>
    <button v-on:click="deleteTodo()">チェック済みの項目を削除する</button>
  </div>
</template>

<script>
export default {
  name: 'Todo',
  methods: {
    addTodo: function () {
      this.items.push({
        title: this.newItemTitle,
        isChecked: false
      }),
      this.newItemTitle = '';
      this.saveTodo();
    },
    saveTodo: function () {
      localStorage.setItem('items', JSON.stringify(this.items));
      localStorage.setItem('defaultItems', JSON.stringify(this.defaultItems));
    },
    loadDefaultTodo: function () {
      this.defaultItems = JSON.parse( localStorage.getItem('defaultItems') );
      if( !this.defaultItems ){
        this.defaultItems = [];
      }
    },
    loadTodo: function () {
      this.items = JSON.parse( localStorage.getItem('items') );
      if( !this.items ){
        this.items = [];
      }
    },
    deleteTodo: function () {
      this.items = this.items.filter(function (item) {
      return item.isChecked === false;
      });
      this.saveTodo();
    },
    defaultTodo: function () {
      this.defaultItems = [
        { index: '1', title: '領収書を準備する' },
        { index: '2', title: 'Vue.jsハンズオンの資料を作る' },
        { index: '3', title: '参加者の人数を確認する' },
        { index: '4', title: 'ピザを注文する' },
        { index: '5', title: '参加費のお釣りを準備する' },
        { index: '6', title: '会場設営をする' },
      ]
    },
  },
  data () {
    return {
      defaultItems: [] ,
      items: [] ,
      newItemTitle: ''
    }
  },
  mounted: function () {
    if( !localStorage.getItem('defaultItems') ){
      this.defaultTodo();
    }else{
      this.loadDefaultTodo();
    }
    this.loadTodo();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.done { text-decoration: line-through; }
a {
  color: #42b983;
}
</style>



