<template>
  <div class="">
    <h1>はじめに</h1>
    <div class="sentence">
      <p>女性の一人暮らしってなんだか、不安が多くて実家を出づらい…<br>
         なんてイメージがありませんか？<br>
         <br>
         ここでは <span class="bold">”一人暮らしを始めたい女性”</span> に、<br>
         ☆こんな風に進めていくよ！というやることリスト<br>
         ☆理想のお部屋を探そう！という物件チェックリスト<br>
         ☆細かいところをチェックしよう！という内見チェックリスト<br>
         ☆最低限これがあれば大丈夫！という引っ越し持ち物チェックリスト<br>
         <br>
         そして、実際に一人暮らしをスタートしたときのリストをそれぞれ作成しました。<br>
         誰かのお役に立てれば幸いです。<br>
         <br>
      </p>
    </div>
    <router-link to="/">Top</router-link>
    <router-link to="/Todo">Todo</router-link>
  </div>
</template>

<script>
export default {
  name: 'Start',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

